import { Component, OnInit } from '@angular/core';
import { TravelService } from '../services/travel.service';

@Component({
  selector: 'app-travels-list',
  templateUrl: './travels-list.component.html',
  styleUrls: ['./travels-list.component.scss']
})
export class TravelsListComponent implements OnInit {

  protected travels: { header: string[]; itemsContent: any[]; };

  constructor(private travelService: TravelService) { }

  ngOnInit() {
    this.travelService.travelsObs.subscribe((data) => {
      this.travels = {
          header: ['Setor Requisitante', 'Destino', 'Ve√≠culo', 'Motorista', 'Data Ida'],
          itemsContent: []
      };

      for (const travelData of data) {
          this.travels.itemsContent.push(travelData);
      }
    });
  }


}



